
/**
 * List all query method names
 *
 * @name GraphQLClient#getQueries
 * @function
 * @memberof GraphQLClient
 * @returns {Array<string>} method name list
 * @example
 * const methods = client.getQueries();
 * // list of query methods
 * // [
 * //   getAccountState,
 * //   getAssetState,
 * //   getFactoryState,
 * //   getDelegateState,
 * //   getTokenState,
 * //   getEvidenceState,
 * //   getForgeState,
 * //   getTx,
 * //   getBlock,
 * //   getBlocks,
 * //   getUnconfirmedTxs,
 * //   getChainInfo,
 * //   getConfig,
 * //   getNetInfo,
 * //   getNodeInfo,
 * //   getValidatorsInfo,
 * //   getForgeStats,
 * //   listAssetTransactions,
 * //   listAssets,
 * //   listBlocks,
 * //   listTopAccounts,
 * //   listTransactions,
 * //   listTokens,
 * //   listFactories,
 * //   getAccountTokens,
 * //   getStakeState,
 * //   listStakes,
 * //   getRollupState,
 * //   listRollups,
 * //   getRollupBlock,
 * //   listRollupBlocks,
 * //   listRollupValidators,
 * //   search,
 * //   estimateGas,
 * // ]
 */

